
import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

//1.定义路由组件
import Guide from "./views/guide.vue";


//2.定义路由
const routes = [
    {
        path:"/",
        component:Guide,
        name:guide,
    }
]


//3.创建路由
const router = new VueRouter({
    router,
    mode:"hash"
}) 


export default router;



